footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 4%;
  background-color: #74c69d;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
}

.link {
  font-size: 1.5em;
  user-select: none;
  text-decoration: none;
  color: black;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.facebook-icon {
  color: #0d8cf0;
}

.instagram-icon {
  color: #e24475;
}

.twitter-icon {
  color: #1d9bf0;
}
